import { useState } from "react";
import classnames from "classnames";
import Paragraph from './paragraph';
import styles from "./index.module.scss";

export default function Chapter({
    id,
    title,
    date,
    content,
}) {
  const [isFlipped, setIsFlipped] = useState(false);
  const chapterCls = classnames(styles.chapter, {
    [styles["no-anim"]]: !isFlipped,
    [styles.flipped]: isFlipped,
  });

  const BookPageLeft = () => (
    <div className={styles["side-1"]} id="p1">
      <div>
        <h2>{ title }</h2>
        <h4>{ date }</h4>
        {
          content.slice(0, 2).map(({ pKey, text, memories }) => (
            <Paragraph
              key={pKey}
              text={text}
              memories={memories}
            />
          ))
        }
      </div>
    </div>
  );

  const BookPageRight = () => (
    <div className={styles["side-2"]} id="p2">
      <div>
        {
          content.slice(1).slice(-2).map(({ pKey, text, memories }) => (
            <Paragraph
              key={pKey}
              text={text}
              memories={memories}
            />
          ))
        }
      </div>
    </div>
  );

  return (
    <div
      style={{
        zIndex: isFlipped ? (id + 1) : (content.length - id),
      }}
      className={chapterCls}
      onClick={(event) => {
        if (!event.target?.src) {
          setIsFlipped(!isFlipped);
        }
      }}
    >
      <BookPageLeft />
      <BookPageRight />
    </div>
  );
}
