// import classnames from 'classnames';
import Chapter from './chapter';
import { resource } from '../content';
import styles from "./index.module.scss";

function Book() {
    return (
        <div
            className={styles.book}
        >
            <div
                className={styles['book-cover']}
                style={{backgroundImage: `url(${process.env.PUBLIC_URL}/us-cover.jpg)`}}
            ></div>
            {
                resource.map(({ id, key, title, date, content}) => (
                    <Chapter
                        id={id}
                        key={key}
                        title={title}
                        date={date}
                        content={content}
                    />
                ))
            }
        </div>
    );
}

export default Book;
