import React from "react";
import cls from 'classnames';
import { Gallery, Item } from "react-photoswipe-gallery";
import "photoswipe/dist/photoswipe.css";

import styles from "./index.module.scss";

const MyGallery = ({ memories }) => {
  const smallItemStyles = {
    cursor: "pointer",
    objectFit: "cover",
    width: "80%",
    maxHeight: "80%",
  };

  return (
    <Gallery withCaption>
      <div
        style={{
            width: '40%',
            marginLeft: '12px'
        }}
        title="Open the Album"
      >
        {memories.map((memory) => (
          <Item key={memory.title} caption={memory.caption} original={memory.url} thumbnail={memory.url}
           width={memory.width} height={memory.height}>
            {({ ref, open }) => (
              <img
                style={smallItemStyles}
                ref={ref}
                onClick={open}
                src={memory.url}
                width="auto!important"
                alt={memory.title}
              />
            )}
          </Item>
        ))}
      </div>
    </Gallery>
  );
};

export default function Paragraph({ text, memories }) {
  return (
    <div className={cls({
        [styles.flex]: memories && memories.length > 0
    }, styles.paragraph)}>
      {text && <span style={{flex: 1}}> {text} </span>}
      {memories && <MyGallery memories={memories} />}
    </div>
  );
}
